<template>
  <button type="button" class="btn btn-danger" :class="[size ? '' : 'btn-sm']" @click="delConfirm()">{{ text ? text : '' }}</button>
</template>

<script>
export default {
  name: 'Delete',
  props: ['item', 'text', 'size'],
  data () {
    return {}
  },
  methods: {
    delConfirm: function () {
      let that = this
      swal({
        title: '',
        text: '',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#DD6B55',
        confirmButtonText: '',
        cancelButtonText: '',
        closeOnConfirm: false
      }, function () {
        that.$emit('delConfirm', that.item, '')
      })
    }
  }
}
</script>



