function approvalStatus (value) {
  let status = ''
  switch (Number(value)) {
    case 1: status = ''; break;
    case 2: status = ''; break;
    case 3: status = ''; break;
    case 4: status = ''; break;
  }
  return status
}

function noticeType (type) {
  let result = ''
  switch (Number(type)) {
    case 1: result = ''; break;
    case 2: result = ''; break;
    case 3: result = ''; break;
  }
  return result
}

import { CODE } from '../constant'
