import Vue from 'vue'
import Router from 'vue-router'

import { COMPONENTS } from './components'
Vue.use(Router)

let routes = new Router({
  mode: 'history',
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/home', component: COMPONENTS.Home, meta: { name: 'home' }  },
    { path: '/notFound', component: COMPONENTS.NotFound },
    { path: '/user', component: COMPONENTS.User },
    { path: '/register', component: COMPONENTS.Register },
    { path: '/login', component: COMPONENTS.Login },
    { path: '/modifyPassword', component: COMPONENTS.ModifyPassword },
    { path: '/editUserPassword/:id', component: COMPONENTS.ModifyPassword },
    // iframe
    {
      path: '/views',
      component: COMPONENTS.Views,
      children: [
        // 
        {
          path: 'collegeIntroduction',
          component: COMPONENTS.CollegeIntroduction,
          children: [ // 
            { path: '/', redirect: '/views/collegeIntroduction/basic' },
            { path: 'basic', component: COMPONENTS.CollegeIntroductionBasicComponent }, // 
            { path: 'branch', component: COMPONENTS.CollegeIntroductionBranchComponent } // 
          ]
        },
        {
          path: 'collegePersonnel',
          component: COMPONENTS.CollegePersonnel,
          children: [ // 
            { path: '/', redirect: '/views/collegePersonnel/scientificResearchPersonnel' },
            { path: 'scientificResearchPersonnel', component: COMPONENTS.CollegePersonnelScientificResearchPersonnelComponent }, // 
            { path: 'scientificResearchPersonnel/:id', component: COMPONENTS.CollegePersonnelScientificResearchPersonnelDetailsComponent },
            { path: 'talentPlan', component: COMPONENTS.CollegePersonnelTalentPlanComponent }, // 
            { path: 'talentPlan/:id', component: COMPONENTS.CollegePersonnelTalentPlanDetailsComponent },
            { path: 'academicCommittee', component: COMPONENTS.CollegePersonnelAcademicCommitteeComponent }, // 
            { path: 'academicCommittee/:id', component: COMPONENTS.CollegePersonnelAcademicCommitteeDetailsComponent },
            { path: 'ethicsCommittee', component: COMPONENTS.CollegePersonnelEthicsCommitteeComponent }, // 
            { path: 'ethicsCommittee/:id', component: COMPONENTS.CollegePersonnelEthicsCommitteeDetailsComponent }
          ]
        },
        {
          path: 'collegeFacilities',
          component: COMPONENTS.CollegeFacilities,
          children: [ // 
            { path: '/', redirect: '/views/collegeFacilities/area' },
            { path: 'area', component: COMPONENTS.CollegeFacilitiesAreaComponent }, // 
            { path: 'area/:id', component: COMPONENTS.CollegeFacilitiesAreaDetailsComponent },
            { path: 'device', component: COMPONENTS.CollegeFacilitiesDeviceComponent }, // 
            { path: 'device/:id', component: COMPONENTS.CollegeFacilitiesDeviceDetailsComponent }
          ]
        },
        // 
        { path: 'academicWorks', component: COMPONENTS.AcademicWorksComponent },  // 
        { path: 'academicWorks/:id', component: COMPONENTS.AcademicWorksDetailsComponent },
        { path: 'hostMeeting', component: COMPONENTS.HostMeetingComponent },  // /
        { path: 'hostMeeting/:id', component: COMPONENTS.HostMeetingDetailsComponent },
        { path: 'meetingPaper', component: COMPONENTS.MeetingPaperComponent },  // 
        { path: 'meetingPaper/:id', component: COMPONENTS.MeetingPaperDetailsComponent },
        { path: 'participateMeeting', component: COMPONENTS.ParticipateMeetingComponent },  // /
        { path: 'participateMeeting/:id', component: COMPONENTS.ParticipateMeetingDetailsComponent },
        { path: 'patent', component: COMPONENTS.PatentComponent },  // 
        { path: 'patent/:id', component: COMPONENTS.PatentDetailsComponent },
        {
          path: 'periodicalPaper',
          component: COMPONENTS.PeriodicalPaper,
          children: [ // 
            { path: '/', redirect: '/views/periodicalPaper/periodicalPaperChinese' },
            { path: 'periodicalPaperChinese', component: COMPONENTS.PeriodicalPaperChineseComponent },  //  - 
            { path: 'periodicalPaperChinese/:id', component: COMPONENTS.PeriodicalPaperChineseDetailsComponent },
            { path: 'periodicalPaperEnglish', component: COMPONENTS.PeriodicalPaperEnglishComponent },  //  - 
            { path: 'periodicalPaperEnglish/:id', component: COMPONENTS.PeriodicalPaperEnglishDetailsComponent }
          ]
        },
        { path: 'researchAwards', component: COMPONENTS.ResearchAwardsComponent },  // 
        { path: 'researchAwards/:id', component: COMPONENTS.ResearchAwardsDetailsComponent },
        {
          path: 'researchProjects',
          component: COMPONENTS.ResearchProjects,
          children: [ // 
            { path: '/', redirect: '/views/researchProjects/country' },
            { path: 'country', component: COMPONENTS.ResearchProjectsCountryComponent },  // 
            { path: 'country/:id', component: COMPONENTS.ResearchProjectsCountryDetailsComponent },
            { path: 'province', component: COMPONENTS.ResearchProjectsProvinceComponent },  // 
            { path: 'province/:id', component: COMPONENTS.ResearchProjectsProvinceDetailsComponent },
            { path: 'other', component: COMPONENTS.ResearchProjectsOtherComponent },  // 
            { path: 'other/:id', component: COMPONENTS.ResearchProjectsOtherDetailsComponent },
            { path: 'landscape', component: COMPONENTS.ResearchProjectsLandscapeComponent },  // 
            { path: 'landscape/:id', component: COMPONENTS.ResearchProjectsLandscapeDetailsComponent }
          ]
        },
        { path: 'researchReport', component: COMPONENTS.ResearchReportComponent },  // 
        { path: 'researchReport/:id', component: COMPONENTS.ResearchReportDetailsComponent },
        { path: 'score', component: COMPONENTS.ScoreComponent },  // 
        { path: 'score/:id', component: COMPONENTS.ScoreDetailsComponent },
        // 
        { path: 'masterTraining', component: COMPONENTS.MasterTrainingComponent },  // 
        { path: 'masterTraining/:id', component: COMPONENTS.MasterTrainingDetailsComponent },
        { path: 'doctorTraining', component: COMPONENTS.DoctorTrainingComponent },  // 
        { path: 'doctorTraining/:id', component: COMPONENTS.DoctorTrainingDetailsComponent },
        { path: 'teacherAward', component: COMPONENTS.TeacherAwardComponent },  // 
        { path: 'teacherAward/:id', component: COMPONENTS.TeacherAwardDetailsComponent },
        { path: 'goodCourse', component: COMPONENTS.GoodCourseComponent },  // 
        { path: 'goodCourse/:id', component: COMPONENTS.GoodCourseDetailsComponent },
        { path: 'teachingMaterialConstruction', component: COMPONENTS.TeachingMaterialConstructionComponent },  // 
        { path: 'teachingMaterialConstruction/:id', component: COMPONENTS.TeachingMaterialConstructionDetailsComponent },
        { path: 'studentAwards', component: COMPONENTS.StudentAwardsComponent },  // 
        { path: 'studentAwards/:id', component: COMPONENTS.StudentAwardsDetailsComponent },
        { path: 'studentWorks', component: COMPONENTS.StudentWorksComponent },  // 
        { path: 'studentWorks/:id', component: COMPONENTS.StudentWorksDetailsComponent },
        { path: 'studentMeeting', component: COMPONENTS.StudentMeetingComponent },  // /
        { path: 'studentMeeting/:id', component: COMPONENTS.StudentMeetingDetailsComponent },
        { path: 'studentStudy', component: COMPONENTS.StudentStudyComponent },  // /
        { path: 'studentStudy/:id', component: COMPONENTS.StudentStudyDetailsComponent },
        { path: 'studentMeetingPaper', component: COMPONENTS.StudentMeetingPaperComponent },  // 
        { path: 'studentMeetingPaper/:id', component: COMPONENTS.StudentMeetingPaperDetailsComponent },
        { path: 'practiceBase', component: COMPONENTS.PracticeBaseComponent },  // 
        { path: 'practiceBase/:id', component: COMPONENTS.PracticeBaseDetailsComponent },
        { path: 'partTimeTutor', component: COMPONENTS.PartTimeTutorComponent },  // 
        { path: 'partTimeTutor/:id', component: COMPONENTS.PartTimeTutorDetailsComponent },
        { path: 'participateProfessionalPractice', component: COMPONENTS.ParticipateProfessionalPracticeComponent },  // 
        { path: 'participateProfessionalPractice/:id', component: COMPONENTS.ParticipateProfessionalPracticeDetailsComponent },
        { path: 'graduateEnrollment', component: COMPONENTS.GraduateEnrollmentComponent },  // 
        { path: 'graduateEnrollment/:id', component: COMPONENTS.GraduateEnrollmentDetailsComponent },
        // 
        { path: 'rules', component: COMPONENTS.RulesComponent }, // 
        { path: 'rules/:id', component: COMPONENTS.RulesDetailsComponent },
        {
          path: 'notice', // 
          component: COMPONENTS.Notice,
          children: [
            { path: '/', redirect: '/views/notice/school' },
            { path: 'school', component: COMPONENTS.NoticeSchoolComponent },
            { path: 'department', component: COMPONENTS.NoticeDepartmentComponent },
            { path: 'writing', component: COMPONENTS.NoticeWritingComponent },
            { path: 'details/:id', component: COMPONENTS.NoticeDetailsComponent }
          ]
        },
        { path: 'customForm', component: COMPONENTS.CustomFormComponent },  // 
        { path: 'customFormDetails', component: COMPONENTS.CustomFormDetailsComponent },
        { path: 'annualBriefing', component: COMPONENTS.AnnualBriefingComponent },  // 
        // 
        { path: 'approval', component: COMPONENTS.ApprovalComponent },
        { path: 'backups', component: COMPONENTS.BackupsComponent },

        { path: 'role', component: COMPONENTS.RoleComponent },
        { path: 'role/:id', component: COMPONENTS.RoleDetailsComponent },
        { path: 'user', component: COMPONENTS.UserComponent },
        { path: 'user/:id', component: COMPONENTS.UserDetailsComponent },

        { path: 'pageSet', component: COMPONENTS.PageSetComponent },
        { path: 'setting', component: COMPONENTS.SettingComponent },
        { path: 'search', component: COMPONENTS.SearchComponent }
      ]
    },
    { path:'*',component: COMPONENTS.NotFound }
    // routes end
  ]
})

import Cache from '@/assets/cache'
routes.beforeEach((to, from, next) => {
  if ((to.path != '/login' && to.path != '/register') && (!Cache.getItem('user', true) || !Cache.getItem('user', true).id)) {
    window.parent.location.href = '/login'
  }
  next()
})

export default routes



